<template>
  <main class="app-main">
    <section class="top">
      <p class="title">
        <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>基本信息
      </p>
    </section>
    <section class="bg-con"></section>
    <div class="con-con">
      <div class="head-portrait">
        <img :src="userInfo.img" alt />
      </div>
      <div class="phone">
        {{ userInfo.userName}}
        <router-link to="/personal/editInfo">
          <!-- <img src="../../../assets/personal/13.png"/> -->
          点击修改
        </router-link>
      </div>
      <md-cell-item title="平台账号" :addon="userInfo.userName" />
      <md-cell-item title="认证身份" :addon=" this.$store.state.user.userInfo.roleId==1?'专家':'单位'" />
      <md-cell-item title="关联电话" :addon="userInfo.phoe" />
      <md-cell-item title="关联邮箱" :addon="userInfo.email" />
      <md-cell-item title="工作单位" :addon="userInfo.unit" />
      <md-cell-item title="工作职务" :addon="userInfo.position" />
      <md-cell-item title="所在地区" :addon="userInfo.province" />
      <md-cell-item title="通讯地址" :addon="userInfo.address" />
    </div>
    <md-button type="default">保存</md-button>
  </main>
</template>

<script>
import { Field, CellItem, Icon, InputItem, Button } from "mand-mobile";
import { getInfo } from "../../../apiUrl/personal/info";

export default {
  name: "index",
  components: {
    [Field.name]: Field,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon,
    [Button.name]: Button,
    [InputItem.name]: InputItem
  },
  data() {
    return {
      userInfo: {},

      userImg: this.$store.state.user.userInfo.phone
    };
  },
  mounted() {
    this.getInfo();
  },
  methods: {
    // 回退
    goBack() {
      this.$router.push("/personal");
    },
    getInfo() {
      getInfo().then(res => {
        this.userInfo = res.data;
      });
    }
  }
};
</script>

