  // background url("../../../assets/personal/bg.png") no-repeat
.con-con
  width: 90%
  margin: 0px auto
  margin-top: -80px
  margin-bottom: 60px
  background: rgba(255, 255, 255, 1)
  box-shadow: 0px 4px 24px 0px rgba(2, 25, 85, 0.1)
  border-radius: 12px
  padding-top: 88px
  .head-portrait
    background: rgba(2, 25, 85, 0)
    border-radius: 12px
    img
      width: 140px
